import axios from '@/utils/uniAxios.js';
// import amap from '../../libs/qqmap-wx-jssdk.js'; 
 import {
 	mapState
 } from 'vuex';
var qqmapsdk;
export default {
	components:{
	},
	data() {
		return {
			addressData: {
			 "addressLabel": "家庭",//地址标签，默认家庭
			  "city": "武汉市",//城市
			  "defaultStatus": 1,//是否默认,0否，1是
			  "detailAddress": "",//详细地址
			  "id": '',
			  "memberId":'',//会员ID
			  "name": "",//收货人姓名
			  "phoneNumber": "",//电话
			  "postCode": "430070",//邮编
			  "province": "湖北省",//省份
			  "region": "洪山区"//区
			},
			address:'',
			addressLabels:['家庭','公司','学校'],
			n:0,
			defaultStatus:true,
			key:'GOUBZ-B3U3R-WEAWX-WE266-WZBW5-JIFUR',//腾讯地图key
			userId:'',
			value1:'全部商品',
			option1: [
			      { text: '全部商品', value: '全部商品' },
			      { text: '新款商品', value:'新款商品' },
			      { text: '活动商品', value: '活动商品' }
			    ],
		}
	},
	computed: {
		...mapState(['hasLogin', 'userInfo'])
	},
	onLoad(option){
		let title = '添加收货地址';
		if(option.type==='edit'){
			title = '编辑收货地址'
			this.addressData = JSON.parse(option.data)
			console.log(this.addressData,777)
		}
		this.manageType = option.type;
		uni.setNavigationBarTitle({
			title
		})
		// 获取id
		this.id = this.$store.state.userInfo.id;
		this.addressData.memberId = this.id;
		// 实例化API核心类
		// qqmapsdk = new amap.QQMapWX({
		// 	key: this.key
		// });
	},
	methods: {
		// 地址标签
		changeList(item,index){
			this.n = index;//this指向app
			this.addressData.addressLabel = item;
		},
		// 切换默认
		switchChange(e){
			this.defaultStatus = e.detail;
			if(this.defaultStatus){
				this.addressData.defaultStatus = 1
			}else{
				this.addressData.defaultStatus = 0
			}
		},
		
		//地图选择地址
		chooseLocation(){
			uni.getLocation({
			    type: 'wgs84',
				geocode:true,
			    success: function (res) {
					// console.log(res)
					
					// 在小程序中不能获取到address的详细信息。需要根据经纬度逆地理编码
					//https://ask.dcloud.net.cn/article/35070
					// https://lbs.qq.com/qqmap_wx_jssdk/
					
			    }
			});
			
			uni.chooseLocation({
				success: (data)=> {
					this.address = data.name;
					// 将经纬度逆地理编码
					var latitude = data.latitude;//维度
					var longitude = data.longitude;//经度
					qqmapsdk.reverseGeocoder({
						location:{
							latitude: latitude,
							longitude: longitude
						},
						success:function(res){
							this.addressData.province='';
							this.addressData.city='';
							this.addressData.region='';
							this.addressData.postCode='';
						}
					})
				}
			})
		},
		// 选择地址
		dropdownChange(e){
			console.log(e)
		},
		//提交
		confirm(){
			this.addressData.address = '华中科技大学'
			let data = this.addressData;
			if(!data.name){
				this.$api.msg('请填写收货人姓名');
				return;
			}
			if(!/^(1[0-9])\d{9}$/.test(data.phoneNumber)){
				this.$api.msg('请输入正确的手机号码');
				return;
			}
			// if(!this.address){
			// 	this.$api.msg('请在地图选择所在位置');
			// 	return;
			// }
			if(!data.detailAddress){
				this.$api.msg('请填写门牌号信息');
				return;
			}
			console.log(this.addressData)
			axios.post('/member/address/add',this.addressData).then(res=>{
				if(res.data.code==200){
					//this.$api.prePage()获取上一页实例，可直接调用上页所有数据和方法，在App.vue定义
					this.$api.prePage().refreshList(data, this.manageType);
					this.$api.msg(`地址${this.manageType=='edit' ? '修改': '添加'}成功`);
					setTimeout(()=>{
						uni.navigateBack()
					}, 800)
				}
				
			})
			
		},
	}
}
